const heroHeight = 115;
const gameBtn = document.querySelector(".header__btn");
const gameTimer = document.querySelector(".header__time");
const gameScore = document.querySelector(".header__score");
const ironManCount = 5;
const gameDuration = 5;
const popUp = document.querySelector(".pop-up");
const popupMessage = document.querySelector(".pop-up__message");
const popupRefresh = document.querySelector(".pop-up__refresh");

let started = false; //ê²Œì„ì´ ì‹œì‘ë˜ì—ˆëŠ”ì§€, ì•ˆë¬ëŠ”ì§€ ì•Œìˆ˜ìˆë„ë¡.
let timer = undefined; // ì–¼ë§ˆë§Œì˜ ì‹œê°„ì´ ë‚¨ì•˜ëŠ”ì§€ ê¸°ì–µí•˜ê¸°ìœ„í•´.
let score = 0; // ìµœì¢…ì ì¸ ì ìˆ˜ë¥¼ ê¸°ì–µí•´ì•¼í•˜ê¸°ë•Œë¬¸ì—.

gameBtn.addEventListener("click", () => {
  // ê²Œì„ì´ ì‹œì‘ë˜ì—ˆë‹¤ë©´, ì¤‘ì§€í•´ì•¼í•˜ëŠ” ê¸°ëŠ¥ì„ ì…ë ¥í•˜ê³ ,
  // ê²Œì„ì´ ì‹œì‘ë˜ì§€ ì•Šì•˜ë‹¤ë©´, ê²Œì„ì‹œì‘ì„ ìœ„í•œ ì„¸íŒ…ì„ í•œë‹¤.
  if (!started) {
    startGame();
  } else if (started) {
    stopGame();
  }
});

//ê²Œì„ì´ ì‹œì‘ë˜ì—ˆì„ ê²½ìš° ì‹¤í–‰ë˜ëŠ” í•¨ìˆ˜ë“¤
function startGame() {
  settingGame();
  showStopBtn();
  showTimeAndScore();
  startGameTimer();
  started = true;
}

//ê²Œì„ì´ ì¤‘ì§€ë˜ì—ˆì„ ê²½ìš° ì‹¤í–‰ë˜ëŠ” í•¨ìˆ˜ë“¤
function stopGame() {
  stopGameTimer();
  hideGameBtn();
  showPopupWidthText("REPLAY?");
  started = false;
}

//ì•„ì´í…œ(hero)ì„ í´ë¦­ì‹œ ë°œìƒí•˜ëŠ” ì´ë²¤íŠ¸
field.addEventListener("click", onfieldClick);

function settingGame() {
  field.innerHTML = "";
  gameScore.innerText = ironManCount;
  addItem("IronMan", 5, "img/IronMan.png");
  addItem("CaptainAmerica", 3, "img/CaptainAmerica");
}

function addItem(className, count, imgPath) {
  const x1 = 0;
  const y1 = 0;
  const x2 = fieldRect.width - heroWidth;
  const y2 = fieldRect.height - heroHeight;
  for (let i = 0; i < count; i++) {
    const item = document.createElement("img");
    item.setAttribute("class", className);
    item.setAttribute("src", imgPath);
    item.style.position = "absolute";
    const x = randomNumber(x1, x2);
    const y = randomNumber(y1, y2);
    item.style.left = `${x}px`;
    item.style.top = `${y}px`;
    field.appendChild(item);
  }
}

function randomNumber(min, max) {
  return Math.random() * (max - min) + min;
}

function showStopBtn() {
  const icon = document.querySelector(".fa-play");
  icon.classList.add("fa-stop");
  icon.classList.remove("fa-play");
}

function showTimeAndScore() {
  gameTimer.style.visibility = "visible";
  gameScore.style.visibility = "visible";
}

function startGameTimer() {
  let remainingTimeSec = gameDuration;
  updateTimerText(remainingTimeSec);
  timer = setInterval(() => {
    if (remainingTimeSec <= 0) {
      clearInterval(timer);
      return;
    }
    updateTimerText(--remainingTimeSec);
  }, 1000);
}

function updateTimerText(time) {
  const minutes = Math.floor(time / 60);
  const seconds = time % 60;
  gameTimer.innerText = `${minutes}:${seconds}`;
}

function stopGameTimer() {
  clearInterval(timer);
}

function hideGameBtn() {
  gameBtn.style.visibility = "hidden";
}

function showPopupWidthText(text) {
  popUp.classList.remove("pop-up--hide");
  popupMessage.innerText = text;
}

function onfieldClick(event) {
  if (!started) {
    return;
  }
  console.log(event);
}
